import request from '../../util/request.js';
export default {
	data() {
		return {
			pageName:'articles',
			categorys:[],
			times:[],
			tags:[]
		}
	},
	onLoad() {
		request.post('/archives').then(res=>{
			console.log(res);
			this.categorys=res.categorys
			this.times=res.times
			this.tags=res.tags
		})
	},
	methods: {
		toList(type,key){
			uni.navigateTo({
				url:'/pages/list/list?type='+type+'&key='+key
			})
		}
	}
}
